
.previous-searchs{
    display: block;
    h2{
        color: var(--text-color);
        margin-bottom: .5em;
    }
    .previous-searchs-container{
        .search-item{
            background-color: var(--primary-color);
            display: inline-block;
            padding: .5em 1em;
            margin: 0 1em 1em 0;
            border-radius: 30px;
            color: #fff;
            box-shadow: 0 3px 6px var(--shadow-color);
            animation: getAnimationDuration(.5s) slideInRight forwards;
            transform: translateX(100%);
            opacity: 0;
      
        }
    }
    .search-box{
        display: flex;
        height: 35px;
        justify-content: flex-end;
        align-items: center;
            input, .btn{
                height: 35px;
            }
            input{
                min-width: 250px;
                padding: 0 .5em;
                border-radius: 5px 0 0 5px;
                outline: none;
                border: 1px solid #4d4d4d;
            }
            .btn{
                padding-block: 0;
                padding-inline: .75em;
                border-radius:  0 5px 5px 0;
            }

    }
  
}

